import React, { Component } from 'react';
import { View, Image, TextInput, StyleSheet } from 'react-native';
import { Text } from 'react-native-svg';
import { Picker } from '@react-native-picker/picker';

class ModalMenuIngredient extends Component {
    render() {
        return (
            <View style={{ borderTop: '1px solid #d9d9d9' }}>
                <View style={styles.textBox}>
                    <View style={styles.textBoxText}>
                        <Text style={styles.title}
                        >{this.props.title}
                        </Text>
                        <TextInput style={styles.subtitle}
                            onChangeText={(text) => this.props.onChangeText(text)}
                            placeholder={this.props.quantity}
                        />

                        <Picker
                            selectedValue={this.value}
                            style={styles.pickerStyle}
                            onValueChange={value => {
                                if (value != "0")
                                    this.props.onUnitChange(value) 
                            }}>
                            <Picker.Item label="Choose Unit" value="0" />
                            <Picker.Item label="Piece" value="Piece" />
                            <Picker.Item label="Gram" value="Gram" />
                            <Picker.Item label="Ounce" value="Ounce" />
                            <Picker.Item label="Liter" value="Liter" />
                        </Picker>
                    </View>
                </View>
            </View>

        )
    }
}

const styles = StyleSheet.create({

    textBox: {
        padding: 8,
        fontSize: 14,
        color: '#000000',
        height: '42px',
        width: '100%',
        backgroundColor: '#F5F5F5',
        textAlignVertical: 'center'
    },

    textBoxText: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        textAlignVertical: 'center',
        top: '20%'
    },

    title: {
        textAlignVertical: 'center',
        fontWeight: 500,
        maxWidth: '60%',
        overflow: 'hidden',
    },

    subtitle: {
        fontWeight: 400,
        backgroundColor: 'white'
    }, 

    pickerStyle: {
        padding: 8,
        fontSize: 15,
        color: '#8E8E93',
        textAlignVertical: 'top',
        borderColor: '#F5F5F5',
        borderTopColor: '#d9d9d9',
        height: '42px',
        width: '100%',
        backgroundColor: '#F5F5F5',
        textIndent: '19px',
        alignSelf: 'flex-end'
    },

});

export default ModalMenuIngredient;